import { useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import axios from "axios";

import { LocalStorage, buffer } from "helpers";
import { routers, API, baseURL } from "API";

import Header from "components/Customer/Header";
import EmptyCart from "components/Customer/EmptyCart";
import CartItem from "components/Customer/CartItem";

import styles from "styles/grid.module.scss";
import clsx from "clsx";

function CartPage() {
  let navigate = useNavigate();

  const [products, setProducts] = useState([]);

  const profile = LocalStorage(buffer("profile"));

  useEffect(() => {
    if (profile) {
      if (localStorage.getItem("access_token")) {
        axios
          .get(`${baseURL}/sanctum/csrf-cookie`)
          .then(
            API.post(routers.cart, { id: profile.get(buffer("id")) }).then(
              (res) => setProducts(res.data)
            )
          );
      } else {
        navigate("/signin");
      }
    } else {
      navigate("/signin");
    }
  }, []);

  return (
    <>
      <Header />
      <div className={clsx(styles.grid, styles.wide, "content")}>
        <div className={clsx(styles.row, styles.no_gutters)}>
          <div className={(styles.l12, styles.m12, styles.c12)}>
            {products.length !== 0 ? (
              products.map((product) => (
                <CartItem key={product.id} props={product} />
              ))
            ) : (
              <EmptyCart />
            )}
          </div>
        </div>
      </div>
    </>
  );
}

export default CartPage;
