import clsx from "clsx";
import { useEffect } from "react";

import { routers } from "API";

import Header from "components/Customer/Header";
import Slide from "components/Customer/Slide";
import Highlightbox from "components/Customer/Highlightbox";
import HotSale from "components/Customer/HotSale";
import ProductHighLight from "components/Customer/ProductHighLight";

import styles from "styles/grid.module.scss";

function HomePage() {
  useEffect(
    () => (document.title = "TaiHoa shop - Sản phẩm công nghệ chính hãng"),
    []
  );
  return (
    <>
      <Header />
      <div className={clsx(styles.grid, styles.wide, "content")}>
        <div className={clsx(styles.row, styles.no_gutters)}>
          <div className={clsx(styles.l12, styles.c12, styles.m12)}>
            <div className={clsx(styles.row, styles.no_gutters)}>
              <Slide />
              <div className={clsx(styles.l3)}>
                <Highlightbox />
              </div>
              <HotSale />
              <ProductHighLight
                title="Điện thoại nổi bật"
                router={routers.phone}
                loop={true}
              />
              <ProductHighLight
                title="Laptop nổi bật"
                router={routers.laptop}
                loop={true}
              />
              <ProductHighLight
                title="Tablet nổi bật"
                router={routers.tablet}
                loop={false}
              />
              <ProductHighLight
                title="Đồng hồ nổi bật"
                router={routers.watch}
                loop={false}
              />
              <ProductHighLight
                title="Tai nghe nổi bật"
                router={routers.speak}
                loop={false}
              />
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default HomePage;
