import clsx from "clsx";
import { useState, useEffect, useLayoutEffect } from "react";
import { useSearchParams } from "react-router-dom";
import axios from "axios";

import { routers, API, baseURL } from "API";

import Header from "components/Customer/Header";
import Slide from "components/Customer/Slide";
import Highlightbox from "components/Customer/Highlightbox";
import ListSearch from "components/Customer/ListSearch";

import styles from "styles/grid.module.scss";

function SearchPage() {
  let [searchParams] = useSearchParams();
  const [products, setProducts] = useState([]);
  let key = searchParams.get("key");

  useLayoutEffect(() => {
    axios
      .get(`${baseURL}/sanctum/csrf-cookie`)
      .then(
        API.post(routers.search, { value: key.trim() }).then((res) =>
          setProducts(res.data)
        )
      );
  }, [key.trim()]);

  useEffect(() => (document.title = `Tìm kiếm: ${key.trim()}`), []);

  return (
    <>
      <Header />
      <div className={clsx(styles.grid, styles.wide, "content")}>
        <div className={clsx(styles.row, styles.no_gutters)}>
          <div className={clsx(styles.l12, styles.c12, styles.m12)}>
            <div
              className={clsx(styles.row, styles.no_gutters, "px-3", "py-5")}
            >
              <Slide />
              <div className={clsx(styles.l3)}>
                <Highlightbox />
              </div>
              {products.length !== 0 ? (
                <>
                  <div className="mt-5 text-info">
                    <h3>Kết quả kìm kiếm: {key}</h3>
                  </div>
                  <ListSearch props={products} loop={false} />
                </>
              ) : (
                <div className="text-danger mt-5">
                  <h3>Không tìm thấy sản phẩm phù hợp với: {key}</h3>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default SearchPage;
