import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import clsx from "clsx";

import { API, routers, baseURL } from "API";

import Header from "components/Customer/Header";
import TitleProductDeTail from "components/Customer/TitleProductDeTail";
import ImageProductDeatil from "components/Customer/ImageProductDeatil";

import styles from "styles/grid.module.scss";

function ProductDetail() {
  let params = useParams();

  const [products, setProducts] = useState([]);

  useEffect(() => {
    axios.get(`${baseURL}/sanctum/csrf-cookie`).then(
      API.post(routers.productDetail, {
        value: params.productName.split("-").join(" "),
      }).then((res) => setProducts(res.data))
    );
  }, []);

  useEffect(
    () => (document.title = params.productName.split("-").join(" ")),
    [params.productName]
  );

  return (
    <>
      <Header />
      <div className={clsx(styles.grid, styles.wide, "content")}>
        <div className={clsx(styles.row, styles.no_gutters)}>
          <div className={clsx(styles.l12, styles.m12, styles.c12)}>
            <TitleProductDeTail
              title={params.productName.split("-").join(" ")}
            />
            <div className={clsx(styles.l4, styles.m6, styles.c12)}>
              {products.map((product) => (
                <ImageProductDeatil key={product.id} props={product} />
              ))}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default ProductDetail;
