import { Route, Routes, Outlet } from "react-router-dom";

import Footer from "./Footer";
import HomePage from "components/Layouts/PublicPage/CustomerLayouts/HomePage";
import SiginPage from "components/Layouts/PublicPage/CustomerLayouts/SiginPage";
import SigupPage from "components/Layouts/PublicPage/CustomerLayouts/SigupPage";
import SearchPage from "components/Layouts/PublicPage/CustomerLayouts/SearchPage";
import ProductDetail from "components/Layouts/PublicPage/CustomerLayouts/ProductDetail";
import CartPage from "components/Layouts/PublicPage/CustomerLayouts/CartPage";

function CustomerLayout() {
  return (
    <>
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/signin" element={<SiginPage props={true} />} />
        <Route path="/signup" element={<SigupPage />} />
        <Route path="/search" element={<SearchPage />} />
        <Route path="/sanpham/:productName" element={<ProductDetail />} />
        <Route path="/cart" element={<CartPage />} />
      </Routes>
      <Outlet />
      <Footer />
    </>
  );
}

export default CustomerLayout;
