import clsx from "clsx";
import { memo } from "react";
import { Link } from "react-router-dom";
import StarOutlinedIcon from "@mui/icons-material/StarOutlined";
import axios from "axios";

import {
  capitalize,
  fmNumber,
  mySwal,
  LocalStorage,
  buffer,
  showToast,
} from "helpers";
import { baseURL, API, routers } from "API";

import classes from "./Product.module.scss";

function Product({ props, star, button }) {
  const profile = LocalStorage(buffer("profile"));
  const handleClick = (e) => {
    const token = localStorage.getItem("access_token");
    if (token) {
      axios.get(`${baseURL}/sanctum/csrf-cookie`).then(
        API.post(routers.addCart, {
          id: e.target.value,
          userId: profile.get(buffer("id")),
        }).then((res) => showToast(res.data.icon, res.data.message))
      );
    } else {
      const Toast = mySwal.mixin({
        toast: true,
        position: "center-center",
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener("mouseenter", mySwal.stopTimer);
          toast.addEventListener("mouseleave", mySwal.resumeTimer);
        },
      });

      Toast.fire({
        icon: "warning",
        title: "Vui lòng đăng nhập",
      });
    }
  };

  return (
    <div id={props.id} className={clsx(classes.productbox)}>
      <div className={clsx(classes.productbox__img)}>
        <Link
          to={`sanpham/${props.name.split(/\s/).join("-")}`}
          className={clsx(classes.product__link)}
        >
          <img
            className={clsx(classes.product__img)}
            src={`${baseURL}/image/imageproducts/${props.image}`}
            alt={props.name}
          />
        </Link>
      </div>
      {props.reduce !== 0 ? (
        <div className={clsx(classes.product__stickerpercent)}>
          <p className={clsx(classes.product__stickerIcon)}>
            {props.reduce !== 0 ? `${props.reduce} %` : ""}
          </p>
        </div>
      ) : null}
      <div className={clsx(classes.product__name)}>
        <Link
          to={`sanpham/${props.name.split(/\s/).join("-")}`}
          className={clsx(classes.product__name_link)}
        >
          <p className={clsx(classes.product__name_item)}>
            {props.name.length <= 42
              ? capitalize(props.name)
              : capitalize(`${props.name.slice(0, 42)} ...`)}
          </p>
        </Link>
      </div>
      <div className={clsx(classes.product_price)}>
        <Link
          to={`sanpham/${props.name.split(/\s/).join("-")}`}
          className={clsx(classes.product_price_link)}
        >
          <p className={clsx("mb-0", classes.olprice)}>
            {fmNumber(props.price - (props.price * props.reduce) / 100)}
          </p>
          <p className={clsx("mb-0 text-muted", classes.newprice)}>
            <del>{props.reduce !== 0 ? fmNumber(props.price) : ""}</del>
          </p>
        </Link>
      </div>
      {star && (
        <div className={clsx(classes.star)}>
          <Link
            to={`sanpham/${props.name.split(/\s/).join("-")}`}
            className={clsx(classes.star__link)}
          >
            <StarOutlinedIcon />
            <StarOutlinedIcon />
            <StarOutlinedIcon />
            <StarOutlinedIcon />
            <StarOutlinedIcon />
          </Link>
        </div>
      )}

      {button && (
        <div className={clsx(classes.button__adcart__box)}>
          <button
            className={clsx(classes.button__adcart)}
            onClick={handleClick}
            value={props.id}
          >
            Thêm vào giỏ hàng
          </button>
        </div>
      )}
    </div>
  );
}

export default memo(Product);
