import { memo } from "react";
import { Swiper, SwiperSlide } from "swiper/react/swiper-react";
import SwiperCore, { Scrollbar, Autoplay, Navigation } from "swiper";
import { useEffect, useState } from "react";
import clsx from "clsx";
import { Link } from "react-router-dom";

import { API, baseURL } from "API";

import classes from "./Slide.module.scss";
import styles from "styles/grid.module.scss";

SwiperCore.use([Scrollbar, Autoplay, Navigation]);

function Slide() {
  const [slides, setSlides] = useState([]);

  useEffect(() => {
    API.get("/slide").then((res) => setSlides(res.data));
  }, []);

  return (
    <div className={clsx(styles.l9, styles.c12, styles.m12)}>
      <Swiper
        scrollbar={{
          hide: false,
        }}
        spaceBetween={30}
        centeredSlides={true}
        autoplay={{
          delay: 2500,
          disableOnInteraction: false,
        }}
        navigation={true}
        className={clsx(classes.mySwiper)}
      >
        {slides.map((slide) => (
          <SwiperSlide key={slide.id}>
            <Link to={`/sanpham/${slide.name.split(/\s/).join("-")}`}>
              <img
                src={`${baseURL}/image/imageslide/${slide.image}`}
                alt={slide.name}
                className={clsx(classes.imgslide)}
              />
            </Link>
          </SwiperSlide>
        ))}
      </Swiper>
      <div>
        <Swiper
          slidesPerView={5}
          spaceBetween={5}
          autoplay={{
            delay: 2500,
            disableOnInteraction: false,
          }}
          loopFillGroupWithBlank={true}
          className={clsx(classes.mySwipertext)}
        >
          {slides.map((slide) => (
            <SwiperSlide key={slide.id} className={clsx(classes.slideText)}>
              <Link
                to={`/sanpham/${slide.name.split(/\s/).join("-")}`}
                className={clsx(classes.slideText__link)}
              >
                <div className={clsx(classes.title)}>{slide.title}</div>
                <div className={clsx(classes.special)}>{slide.special}</div>
              </Link>
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
    </div>
  );
}

export default memo(Slide);
