import { memo, useEffect, useState } from "react";
import clsx from "clsx";

import Product from "../Product";
import { Swiper, SwiperSlide } from "swiper/react/swiper-react";
import SwiperCore, { Navigation, Autoplay } from "swiper";

import styles from "styles/grid.module.scss";
import classes from "./ListSearch.module.scss";

SwiperCore.use(Navigation, Autoplay);

function ListSearch({ props, title, loop }) {
  const [item, setItem] = useState(5);
  const [sizeSrceen, setSizeSrceen] = useState(window.innerWidth);
  useEffect(() => {
    const handleResize = () => {
      setSizeSrceen(window.innerWidth);
      if (sizeSrceen >= 1024) {
        setItem(5);
      } else {
        setItem(3);
      }
    };
    window.addEventListener("resize", handleResize);

    return () => window.removeEventListener("resize", handleResize);
  }, []);
  return (
    <div
      className={clsx(
        "py-3 ",
        styles.l12,
        styles.m12,
        styles.c12,
        classes.listproductbox
      )}
    >
      <div
        className={clsx(
          "d-flex flex-column rounded align-iems-center",
          classes.boxtitle
        )}
      >
        <div
          className={clsx(
            "px-3 py-2",
            classes.boxtitle__title,
            styles.l12,
            styles.m12,
            styles.c12
          )}
        >
          {title && (
            <p className={clsx("h3 mb-0 font-wieght-bold")}>
              <strong>
                <i>{`${title}`}</i>
              </strong>
            </p>
          )}
        </div>
        <Swiper
          className={clsx(styles.l12, styles.m12, styles.c12, classes.mySwiper)}
          slidesPerView={item}
          navigation={true}
          loop={loop}
        >
          {props.map((product) => (
            <SwiperSlide
              key={product.id}
              className={clsx(classes.mySwiperSlide)}
            >
              <Product props={product} button={true} />
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
    </div>
  );
}

export default memo(ListSearch);
