import axios from "axios";

export const baseURL = "http://127.0.0.1:8000";
let API;
const token = localStorage.getItem("access_token");
if (typeof token !== undefined) {
  API = axios.create({
    baseURL: `${baseURL}/api/`,
    headers: {
      Accept: "application/json",
      Authorization: `${token}`,
    },
    withCredentials: true,
  });
} else {
  API = axios.create({
    baseURL: `${baseURL}/api/`,
    headers: {
      Accept: "application/json",
    },
    withCredentials: true,
  });
}

export default API;
