import HeaderA from "components/Admin/HeaderA";
import NavBarA from "components/Admin/NavBarA";

import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import RemoveCircleOutlineIcon from "@mui/icons-material/RemoveCircleOutline";
import { Button } from "react-bootstrap";

import { baseURL, API, routers } from "API";
import { fmNumber, mySwal, showToast } from "helpers";
import axios from "axios";

function ProductsPage() {
  const [products, setProducts] = useState([]);

  useEffect(() => {
    API.get(routers.admin.products).then((res) => {
      setProducts(res.data);
      //   setCategories(res.data.category);
      //   setManu(res.data.manufacturer);
      //   setProm(res.data.promotion);
    });
  }, []);

  const handleClick = (e) => {
    axios.get(`${baseURL}/sanctum/csrf-cookie`).then(
      API.post("/admin/removeProduct", { id: e.target.value }).then((res) => {
        showToast("success", res.data.message);
        document.getElementById(res.data.id).remove();
      })
    );
  };
  return (
    <>
      <HeaderA />
      <NavBarA />
      <div className="content-wrapper">
        <section className="content-header">
          <div className="container-fluid">
            <div className="row mb-2">
              <div className="col-sm-6">
                <h1>DataTables</h1>
              </div>
              <div className="col-sm-6">
                <ol className="breadcrumb float-sm-right">
                  <li className="breadcrumb-item">
                    <Link to="#">Home</Link>
                  </li>
                  <li className="breadcrumb-item active">DataTables</li>
                </ol>
              </div>
            </div>
          </div>
        </section>
        <section className="content">
          <div className="container-fluid">
            <div className="row">
              <div className="col-12">
                <div className="card">
                  <div className="card-header">
                    <h3 className="card-title">DataTable products</h3>
                  </div>
                  <div className="card-body">
                    <table
                      id="example2"
                      className="table table-bordered table-hover"
                    >
                      <thead>
                        <tr>
                          <th>Tên</th>
                          <th>Giá</th>
                          <th>Giảm giá</th>
                          <th>Danh mục</th>
                          <th>Hãng sản xuất</th>
                          <th>Khuyến mãi</th>
                          <th>Sale</th>
                          <th>Status</th>
                          <th>Thao tác</th>
                        </tr>
                      </thead>
                      <tbody>
                        {products &&
                          products.map((product) => (
                            <tr key={product.id} id={product.id}>
                              <td>{product.name}</td>
                              <td>{fmNumber(product.price)}</td>
                              <td>{`${product.reduce} %`}</td>
                              <td>{product.categorie_name}</td>
                              <td>{product.manufacturer_name}</td>
                              <td>{product.promotion_name}</td>
                              <td>
                                {product.sale === 1 ? (
                                  <CheckCircleIcon className="text-success" />
                                ) : (
                                  <RemoveCircleOutlineIcon className="text-danger" />
                                )}
                              </td>
                              <td>
                                {product.status === 1 ? (
                                  <CheckCircleIcon className="text-success" />
                                ) : (
                                  <RemoveCircleOutlineIcon className="text-danger" />
                                )}
                              </td>
                              <td>
                                <Link
                                  className="btn btn-success"
                                  to={`/admin/changeproduct?id=${product.id}`}
                                >
                                  Sửa
                                </Link>
                                <Button
                                  variant="danger"
                                  value={product.id}
                                  onClick={handleClick}
                                >
                                  Xóa
                                </Button>
                              </td>
                            </tr>
                          ))}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
}

export default ProductsPage;
