import { useEffect } from "react";
import Sigin from "components/Customer/Sigin";
import clsx from "clsx";

import Header from "components/Customer/Header";

import styles from "styles/grid.module.scss";

function SiginPage({ props }) {
  useEffect(() => (document.title = " Đăng nhập"), []);
  return (
    <>
      {props && <Header />}
      <div className={clsx(styles.grid, styles.wide, "content")}>
        <div className={clsx(styles.row, styles.no_gutters)}>
          <div className={clsx(styles.l12, styles.c12, styles.m12)}>
            <div className="px-4">
              <div className="text-center mb-3">
                <h3>Đăng nhập</h3>
              </div>
              <Sigin />
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default SiginPage;
