import clsx from "clsx";
import { useNavigate } from "react-router-dom";

import { API, baseURL, routers } from "API";
import { capitalize, fmNumber, mySwal, showToast } from "helpers";

import classes from "./Cartitem.module.scss";
import axios from "axios";
import { useState } from "react";

function CartItem({ props, flag }) {
  let navigate = useNavigate();
  const [quantity, setQuantity] = useState(props.quantity);
  const handleClick = (e) => {
    axios.get(`${baseURL}/sanctum/csrf-cookie`).then(
      API.post(routers.removeCart, { id: e.target.value }).then((res) => {
        showToast("success", res.data.message);
        document.getElementById(res.data.id).remove();
        if (
          document.getElementsByClassName(classes.cartitem_box).length === 0
        ) {
          navigate("/");
        }
      })
    );
  };

  const handleMinus = (e) => {
    axios.get(`${baseURL}/sanctum/csrf-cookie`).then(
      API.post(routers.changeQuantity, {
        id: e.target.value,
        action: "minus",
      }).then((res) => {
        showToast("success", res.data.message);
        setQuantity(res.data.quantity);
      })
    );
  };

  const handlePlus = (e) => {
    axios.get(`${baseURL}/sanctum/csrf-cookie`).then(
      API.post(routers.changeQuantity, {
        id: e.target.value,
        action: "plus",
      }).then((res) => {
        showToast("success", res.data.message);
        setQuantity(res.data.quantity);
      })
    );
  };

  return (
    <div className={clsx(classes.cartitem_box)} id={props.id}>
      <div className={clsx(classes.cartitem_group)}>
        <div className={clsx(classes.cartitem__img)}>
          <img
            src={`${baseURL}/image/imageproducts/${props.image}`}
            className={clsx(classes.img)}
          />
        </div>
        <div className={clsx(classes.cartitem_info)}>
          <div className={clsx(classes.item_name)}>
            <div className={clsx(classes.name)}>{capitalize(props.name)}</div>
          </div>
          <div className={clsx(classes.pricebox)}>
            <div className={clsx(classes.price)}>
              {fmNumber(props.price - (props.price * props.quantity) / 100)}
            </div>
            <div className={clsx(classes.oldprice)}>
              <del>{fmNumber(props.price)}</del>
            </div>
          </div>
          <div className={clsx(classes.buttongroup)}>
            <div className={clsx(classes.minu_btn)}>
              <button
                className={clsx(classes.btn, "btn")}
                value={props.id}
                onClick={handleMinus}
                disabled={quantity === 1}
              >
                -
              </button>
            </div>
            <div className={clsx(classes.quantity)}>{quantity}</div>
            <div className={clsx(classes.plus_btn)}>
              <button
                className={clsx(classes.btn, "btn")}
                value={props.id}
                onClick={handlePlus}
                disabled={quantity === 5}
              >
                +
              </button>
            </div>
          </div>
        </div>
        <div className={clsx(classes.cart_action)}>
          <button
            onClick={handleClick}
            className={clsx(classes.button)}
            value={props.id}
          >
            Xóa
          </button>
        </div>
      </div>
    </div>
  );
}

export default CartItem;
