import { memo } from "react";
import { useNavigate, Link } from "react-router-dom";
import clsx from "clsx";
import LogoutIcon from "@mui/icons-material/Logout";

import classes from "./Account.module.scss";
// import { Button } from "react-bootstrap";
import { baseURL } from "API";
import { LocalStorage, buffer } from "helpers";

function Account() {
  let navigate = useNavigate();
  const handleSignout = () => {
    localStorage.removeItem("access_token");
    localStorage.removeItem(buffer("profile"));
    navigate("/signin");
  };

  let profile = LocalStorage(Buffer.from("profile").toString("base64"));

  let image = profile.get(Buffer.from("image").toString("base64"));

  let name = profile.get(Buffer.from("name").toString("base64"));

  return (
    <div className={clsx(classes.accountbox)}>
      <div className={clsx(classes.accountbox__child)}>
        <Link to="/users" className={clsx(classes.account__link)}>
          <div className={clsx(classes.account)}>
            <div className={clsx(classes.account__img)}>
              <img src={`${baseURL}${image}`} className={clsx(classes.img)} />
            </div>
            <div className={clsx(classes.name)}>
              <span>{name}</span>
            </div>
          </div>
        </Link>
      </div>
      <div onClick={handleSignout} className={clsx(classes.signout)}>
        Đăng xuất
      </div>
    </div>
  );
}

export default memo(Account);
