import { useEffect, useState, memo } from "react";
import clsx from "clsx";
import { Link } from "react-router-dom";

import { API, routers, baseURL } from "API";

import classes from "./Highlightbox.module.scss";

function HighlightBox() {
  const [images, setImages] = useState([]);

  useEffect(() => {
    API.get(routers.highlightbox).then((res) => setImages(res.data));
  }, []);

  return (
    <div className={clsx(classes.highlightbox)}>
      {images.map((image) => (
        <div key={image.id} className={clsx(classes.highlightbox__item)}>
          <Link to={image.name} className={clsx(classes.highlightbox__link)}>
            <img
              src={`${baseURL}/image/imagefrontend/${image.image}`}
              alt={image.name}
              className={clsx(classes.highlightbox__img)}
            />
          </Link>
        </div>
      ))}
    </div>
  );
}

export default memo(HighlightBox);
