import { memo, useState, useEffect, useRef } from "react";
import styles from "styles/grid.module.scss";
import clsx from "clsx";
import SwiperCore, { Navigation, Autoplay } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react/swiper-react";

import flashSale from "./icon/flash-sale.gif";

import Product from "../Product";
import { API, routers } from "API";

import classes from "./Hotsale.module.scss";

SwiperCore.use([Autoplay, Navigation]);

function Hotsale() {
  const [products, setProducts] = useState([]);
  const [slidePerview, setSlidePerview] = useState(5);

  useEffect(() => {
    API.get(routers.hotsale)
      .then((res) => setProducts(res.data))
      .catch((err) => console.log(err));
  }, []);

  const [remaining, setRemaining] = useState();
  const timerId = useRef();
  const nowDate = useRef();
  useEffect(() => {
    const endDate = new Date("01/31/2022");
    timerId.current = setInterval(() => {
      nowDate.current = new Date();
      setRemaining(endDate.getTime() - nowDate.current.getTime());
    }, 1000);

    return () => clearInterval(timerId.current);
  }, []);

  let seconds = ("00" + Math.floor((remaining % 60000) / 1000)).slice(-2);
  let minutes = ("00" + Math.floor((remaining % 3600000) / 60000)).slice(-2);
  let hours = ("00" + Math.floor((remaining % (3600000 * 24)) / 3600000)).slice(
    -2
  );
  let days = ("00" + Math.floor(remaining / (3600000 * 24))).slice(-2);

  return (
    <div
      className={clsx(
        "bg-white rounded shadow-sm ",
        styles.l12,
        styles.m12,
        styles.c12,
        classes.boxflashsale
      )}
    >
      <div
        className={clsx(
          "d-flex rounded align-items-center px-2",
          classes.box_title
        )}
      >
        <div
          className={clsx(
            styles.l6,
            styles.c6,
            styles.m6,
            classes.box_title__title
          )}
        >
          <div className="d-flex justify-content-start">
            <p className={clsx("mb-0", classes.text__hotsale)}>
              <strong>
                HOT
                <img
                  src={flashSale}
                  alt="icon-flash-sale"
                  className={clsx(classes.icon__hotsale)}
                />
                SALE
              </strong>
            </p>
          </div>
        </div>
        <div
          className={clsx(
            styles.l6,
            styles.m6,
            styles.c6,
            classes.boxtitle__date
          )}
        >
          <div className="d-flex justify-content-end align-items-center">
            <p className={clsx("mb-0", classes.title)}>Bắt đầu sau</p> &nbsp;
            <ul className="mb-0 d-flex">
              <li>
                <span className={clsx(classes.boxtitle__date_text)}>
                  {days}
                </span>
                :
              </li>
              <li>
                <span className={clsx(classes.boxtitle__date_text)}>
                  {hours}
                </span>
                :
              </li>
              <li>
                <span className={clsx(classes.boxtitle__date_text)}>
                  {minutes}
                </span>
                :
              </li>
              <li>
                <span className={clsx(classes.boxtitle__date_text)}>
                  {seconds}
                </span>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <Swiper slidesPerView={slidePerview} navigation={true} loop={true}>
        {products.map((product) => (
          <SwiperSlide key={product.id} className={clsx(classes.mSwiperSlide)}>
            <Product props={product} star={true} button={true} />
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
}

export default memo(Hotsale);
